@import url('https://fonts.googleapis.com/css?family=Titillium+Web');

$tim: #87509c; //Tím
$ngoc: #17c2a4; // Cyan
$vang: #ffdd99; // Vang
$do: #d74680; // Đỏ
$xanh: #3c5499; // Xanh
$cam: #eb7d4b; // Cam
$xam: #e7f1f8; // Xám
$chu: #3c4761; // màu chữ
$datroi: #30bae7; // xanh da trời

body {
    font-family: 'Titillium Web', sans-serif; }

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
    font-weight: 600; }

a:hover, a {
    text-decoration: none; }

a {
    color: $chu; }

.form-control::-webkit-input-placeholder {
    color: #fff; }

/*Một số code dùng chung*/
@mixin base-beaf {
    &:before,
    &:after {
        content: ' ';
        display: block;
        position: absolute;
        left: 50%;
        background: #000000;
        opacity: .15;
        transform: translateX(-50%); }
    &:before {
        width: 60px;
        height: 3px;
        bottom: -10px; }
    &:after {
        width: 10px;
        height: 10px;
        bottom: -14px; } }

@mixin flex {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center; }

/*Css cho phần social link*/
a.social_icon {
    background-color: #bdd1df;
    margin-left: 10px;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    transition: .5s;
    @include flex;
    display: inline-flex;
    i {
        color: #fff; }
    &:hover {
        background-color: $xanh;
        transition: .5s; } }



/*Custom css cho các phần section*/
.home_slider {
    background: $tim;
    min-height: 368px;
    @include flex;
    h1 {
        font-size: 2rem;
        color: #ffff;
        letter-spacing: 1px; }
    .btn-more {
        background: $cam;
        font-size: .9rem;
        color: #ffffff;
        font-weight: 600;
        padding: 15px 60px;
        letter-spacing: 1px;
        &:hover {
            box-shadow: 0 .125rem .25rem rgba(0,0,0,.275) !important; } } }

.home_service {
    background: $ngoc;
    min-height: 568px;
    @include flex;
    h2 {
        position: relative;
        margin-bottom: 1.5rem;
        font-weight: 700;
        color: #ffffff;
        letter-spacing: 1px;
        @include base-beaf; }
    p,
    h3 {
        color: #ffffff; }
    h3 {
        font-size: 1.3rem;
        padding: 30px 0 0 0; }
    img {
        max-width: 140px;
        padding-top: 40px; } }


.home_team {
    min-height: 668px;
    background-color: $xam;
    @include flex;
    h2 {
        position: relative;
        margin-bottom: 1.5rem;
        font-weight: 700;
        color: $chu;
        letter-spacing: 1px;
        @include base-beaf; }
    p,
    h3 {
        color: $chu; }
    h3 {
        font-size: 1.3rem;
        padding: 30px 0 0 0; }
    h6 {
        font-size: 1rem;
        color: $datroi; }
    img {
        max-width: 140px;
        height: 140px;
        border-radius: 50%;
        background-color: $xam;
        margin-top: 40px; } }

.home_skill {
    min-height: 568px;
    background-color: #fff;
    @include flex;
    h2 {
        position: relative;
        margin-bottom: 1.5rem;
        font-weight: 700;
        color: $chu;
        letter-spacing: 1px;
        @include base-beaf; }
    h3 {
        color: $chu;
        font-size: 1.3rem;
        padding: 30px 0 0 0; } }

.circle_padding {
    position: relative;
    margin-top: 40px;
    p {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        text-align: center;
        line-height: 140px;
        font-size: 40px; } }

.home_portfolio {
    background: $vang;
    min-height: 1168px;
    @include flex;
    h2 {
        position: relative;
        margin-bottom: 1.5rem;
        font-weight: 700;
        color: $chu;
        letter-spacing: 1px;
        @include base-beaf; } }

.portfolio_menu {
    p {
        display: inline-block;
        background-color: rgba(0,0,0,.15);
        padding: 8px 0;
        margin-left: 10px;
        width: 120px;
        border-radius: 5px;
        transition: .5s;
        font-weight: bold;
        &:hover {
            background-color: #ffffff;
            transition: .5s;
            box-shadow: 0 .125rem .25rem rgba(0,0,0,.275) !important; }
        a:hover {
            color: $chu; } } }

.portfolio_custom {
    padding: 30px 10px 0 10px;
    p {
        padding-top: 20px;
        font-weight: bold; } }

.port_button {
    padding: 15px 60px;
    background-color: $ngoc;
    color: #fff;
    margin-top: 20px;
    &:hover {
        color: #fff;
        box-shadow: 0 .125rem .25rem rgba(0,0,0,.575) !important; } }

.contact_button {
    padding: 15px 60px;
    background-color: $datroi;
    color: #fff;
    margin-top: 20px;
    &:hover {
        color: #fff;
        box-shadow: 0 .125rem .25rem rgba(0,0,0,.575) !important; } }

.home_people {
    background: $do;
    min-height: 668px;
    color: #fff;
    @include flex;
    h2 {
        position: relative;
        margin-bottom: 1.5rem;
        font-weight: 700;
        letter-spacing: 1px;
        @include base-beaf; }
    .col-md-4 {
        img {
            width: 100px;
            height: 100px;
            border-radius: 50%; } }
    .col-md-8 {
        .testimonial {
            text-align: left; }
        p {
            font-style: italic;
            min-height: 72px; } }
    .row {
        padding-top: 40px; }
    h6 {
        color: $vang; } }



.home_contact {
    background: $xanh;
    min-height: 668px;
    color: #fff;
    @include flex;
    h2 {
        position: relative;
        margin-bottom: 2.5rem;
        font-weight: 700;
        letter-spacing: 1px;
        @include base-beaf; }
    p {
        margin-bottom: 2.5rem; }

    .form-group {
        .form-control {
            color: #fff;
            background-color: #273a71;
            border: none;
            margin-top: 10px;
            min-height: 50px;
            textarea {
                color: #fff;
                background-color: #273a71; } } } }


/* header */
header {
    background-color: $tim;
    padding: 40px 0;
    .menu_box {
        display: inline-flex;
        color: #fff;
        float: right;
        p {
            padding: 5px 15px;
            margin-left: 10px;
            &:hover {
                background-color: rgba(0,0,0,.2); } } } }

.footer {
    background-color: #344b8e;
    padding: 40px 0;
    .footer_box {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        color: #fff;
        width: 100%;
        p {
            padding: 0px 1rem;
            margin-bottom: 0;
            font-size: 1.1rem;
            font-weight: bold;
            &:hover {
                color: #30bae7; } } } }

/* navbar */
.navbar-light {
    background-color: $tim !important; }
